<template>
  <div class="reset-password">
    <pwd-crumbs></pwd-crumbs>
    <form action="" @submit.prevent="submit" class="comForm" autocomplete="off">
      <div class="input-wrapper">
        <input type="password" v-model="sendForm.pwd" :class="errorPwd?'active':''"  @focus="focus($event,'pwd')" @blur="blur($event,'pwd')" placeholder="密码">
        <i class="del" v-show="sendForm.pwd" @mousedown.stop="del('pwd')" :style="delImg"></i>
        <span class="error" >{{errorPwd}}</span>
      </div>
      <div class="input-wrapper">
        <input type="password" v-model="sendForm.chkPwd"   @focus="focus($event)" @blur="blur($event)" placeholder="确认密码">
        <i class="del" v-show="sendForm.chkPwd" @mousedown.stop="del('chkPwd')" :style="delImg"></i>
      </div>
      <div class="submit-wrapper">
        <p class="pwd-tips">密码仅可设置为4-20位的数字、英文、英文符号，不能包含空格</p>
        <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
        <input type="submit" value="重置密码" class="submit" :class="allValid? 'active':''" :disabled="!allValid" >
      </div>
      <div class="go-login-tips">
        已有账号，马上<router-link tag="span" to="/37duc/user/smsLogin" class="register-btn"><span>登录</span></router-link>
      </div>
    </form>
  </div>
</template>

<script>
import pwdCrumbs from '@/components/password/ThePwdCrumbs'
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
import { pwdErrorTips } from '@/assets/js/errorTips'
import { mapGetters } from 'vuex'
export default {
  name: 'TheIdentifyVailid',
  mixins: [getComFormMixin],
  data () {
    return {
      sendForm: {
        pwd: '',
        chkPwd: ''
      }
    }
  },
  mounted () {
    this.getNormalStep()
  },
  methods: {
    submit () {
      // commit('SET_FORGETPWDSTEP')
      if (this.sendForm.pwd !== this.sendForm.chkPwd) {
        this.errorTips = pwdErrorTips.notSame
        return
      }
      let postData = {
        password: this.sendForm.pwd,
        rePassword: this.sendForm.chkPwd
      }
      this.$api.user.resetPassword(postData).then(res => {
        if (res.code === 1) {
          this.$router.push({
            path: '/37duc/user/forgetPassword/succeed'
          })
        }
      })
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'pwd':
          this._validPwd()
          break
          // case 'pwd':
          //   this.errorPwd = ValidateUtil.pwdRegexs(this.pwdLoginForm.pwd)
          //   break
      }
    },
    getNormalStep () {
      this.$api.user.isNormalByStep2().then(res => {
        if (res.code === 0) {
          this.$router.push({
            path: '/37duc/user/forgetPassword/mobileValid'
          })
        }
      })
    },
    _validPwd () {
      if (this.sendForm.pwd) {
        this.errorPwd = ValidateUtil.pwdRegexs(this.sendForm.pwd).tip
      }
    }
    // ...mapMutations(['SET_FORGETPWD_STEP'])
  },
  components: {
    pwdCrumbs
  },
  computed: {
    allValid () {
      return this.sendForm.pwd.length >= 4 && this.sendForm.chkPwd.length >= 4 && !this.errorPwd
    },
    ...mapGetters(['forgetPwdStep'])
  }

}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
  .comForm{
    padding-top:10px;
    .submit-wrapper{
      .error-tips{
        top:36px;
      }
      .pwd-tips{
        font-size: 14px;
        color: #B6BBBB;
        padding-top:10px;
        margin-top: -24px;
      }
    }
  }
</style>
